body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: #000046; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
        to right,
        #1cb5e0,
        #000046
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
@font-face {
    font-family: "MR ROBOT FONT";
    font-style: normal;
    font-weight: normal;
    src: local("MR ROBOT FONT"), url("goodwin.christopher.github.io/robofriends/static/media/MR ROBOT.ecf427f7.ttf") format("woff");
}

h1 {
    font-family: "MR ROBOT FONT";
    font-weight: 200;
    color: #8ccee4;
}

